.hex {
  // opacity: 0.1; //delete

  // width: 180px;
  // height: 280px;
  width: 20vmin;
  // max-width: 23vmin;
  height: 31vmin;
  // max-height: 36vmin;

  @media screen and (max-width: 414px) and (max-height: 920px) {
    // @media screen and (width: 375px) and (height: 667px) {
    width: 24vmin;
    height: 38vmin;
  }

  background: #FFF;
  position: absolute;
  // transform: translate(-50%, -50%);
  // transform: translateX(-50%) translateX(50%);
  border-radius: 10%;
  transition: left .4s,
  bottom .4s,
  right .4s,
  top .4s,
  transform .4s,
  visibility .4s,
  opacity .4s;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    // background: rgb(187, 187, 187);
    background: inherit;
    top: 0;
    left: 0;
    border-radius: inherit;
    z-index: -1;
  }

  &::before {
    transform: rotate(60deg);
  }

  &::after {
    transform: rotate(-60deg);
  }

  &Active {
    // background-color: pink;
    // padding: 3vmin 0;
    text-align: center;
    // height: inherit;
    // transition: padding 2s;

    .hexWrapper {
      position: absolute;
      // top: 50%;
      left: 50%;
      transform: translateX(-50%);
      padding: 3vmin 0;
      height: inherit;
      width: 120%;
    }

    .location {
      font-size: 3.0vmin;
      // font-size: 3vmin;
      margin-bottom: 4vmin;
    }

    .date {
      margin-bottom: 1.5vmin;
      font-size: 4.0vmin;
      font-weight: 800;
      text-align: center;
      text-transform: uppercase;
    }

    .time {
      margin-bottom: 3.0vmin;
      font-size: 3.0vmin;
    }

    button {
      background-color: inherit;
      padding: 1.2vmin 2vmin;
      border: 1px solid black;
      border-radius: .5vmin;
      font-size: 1.8vmin;
      // position: absolute;
      // bottom: 4vmin;
      // width: 70%;
      // left: 50%;
      // transform: translateX(-50%);
    }
  }

  &Passive p {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    font-size: 5vmin;
    font-weight: 400;
  }
}

.hexPosition {

  &0 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 0%;
    bottom: 19%;
  }

  &1 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 0%;
    bottom: 19%;
  }

  &2 {
    transform: translate(-50%, 50%) scale(0.36);
    left: 9%;
    bottom: 25%;
  }

  &3 {
    transform: translate(-50%, 50%) scale(0.6);
    left: 25%;
    bottom: 35%;
  }

  &4 {
    transform: translate(-50%, 50%) scale(1);
    left: 50%;
    bottom: 50%;
  }

  &5 {
    transform: translate(-50%, 50%) scale(0.6);
    left: 75%;
    bottom: 65%;
  }

  &6 {
    transform: translate(-50%, 50%) scale(0.36);
    left: 91%;
    bottom: 75%;
  }

  &7 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 100%;
    bottom: 81%;
  }

  &8 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 100%;
    bottom: 81%;
  }

  &0,
  &1,
  &7,
  &8 {
    visibility: hidden;
    opacity: 0;
  }
}

.isHidden {
  visibility: hidden;
  opacity: 0;
}

.firstRival {
  width: 310px; // 250
  height: 140px;
  background: #fff;
  position: absolute;
  // transform: skew(-30deg);
  left: 0%;
  bottom: 50%;
  z-index: 0;

  &::before {
    content: '';
    width: 90px;
    height: 140px;
    background: inherit;
    transform: translate(50%) skew(-30deg);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 5px;
    position: absolute;
    right: 0;
    // z-index: 1;
    // opacity: 0.5;
  }

  &::after {
    content: '';
    width: 460px;
    height: 140px;
    background: inherit;
    position: absolute;
    top: 0;
    right: 310px;
  }
}

.secondRival {
  width: 310px; // 250
  height: 140px;
  background: #fff;
  position: absolute;
  right: 0%;
  top: 50%;
  z-index: 0;

  &::before {
    content: '';
    width: 90px;
    height: 140px;
    background: inherit;
    transform: translate(-50%) skew(-30deg);
    border-bottom-left-radius: 10px;
    border-top-left-radius: 5px;
    position: absolute;
    left: 0;
    // z-index: 1;
    // opacity: 0.5;
  }

  &::after {
    content: '';
    width: 460px;
    height: 140px;
    background: inherit;
    position: absolute;
    top: 0;
    left: 310px;
    background-color: inherit;
  }
}

.firstRivalName {
  position: absolute;
  right: 0;
  margin: 50px 0;
  text-align: left;
  font-size: 40px;
  font-weight: 800;
}

.secondRivalName {
  position: absolute;
  left: 0;
  margin: 50px 0;
  text-align: right;
  font-size: 40px;
  font-weight: 800;
}